@import url(https://fonts.googleapis.com/css2?family=Righteous&display=swap);
.header-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 150px;
  width: 100%;
}

.header-container h1 {
  color: #e2e2e2;
  font-size: 6rem;
  font-family: "Righteous", cursive;
  cursor: default;
  text-shadow: 2px 0px 20px rgba(0, 0, 0, 0.5);
}

.header-container p {
  font-size: 1.5rem;
  text-shadow: 2px 0px 20px rgba(0, 0, 0, 0.5);
}

@media only screen and (max-width: 769px) {
  .header-container {
    margin-top: 80px;
    background-image: none;
  }
  .header-container h1 {
    font-size: 3rem;
  }
  .header-container p {
    font-size: 1rem;
    text-align: center;
    padding: 1rem;
  }
}

.coin-cards-container {
  display: grid;
  width: 100%;
  grid-template-columns: 1fr repeat(12, minmax(auto, 60px)) 1fr;
  grid-gap: 40px;
  padding: 20px 0;
}

.cards {
  grid-column: 2 / span 12;
  display: grid;
  grid-template-columns: repeat(12, minmax(auto, 60px));
  grid-gap: 40px;
}

.card {
  grid-column-end: span 4;
  display: flex;
  flex-direction: column;
  background-color: #041a29;
  color: white;
  cursor: pointer;
  transition: all 0.3s ease 0s;
}

.card-price {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.card p {
  color: ;
  margin: 0;
}

.card-price-up {
  color: #2cb362;
}

.card-price-down {
  color: #bd5e74;
}

.card:hover {
  -webkit-transform: translateY(-7px);
          transform: translateY(-7px);
}

.card img {
  width: 48px;
  height: 48px;
  margin-right: 10px;
}

.card-price {
  padding-bottom: 8px;
  font-size: 1.6rem;
}

.card-rank {
  padding-right: 5px;
  font-size: 2rem;
  color: #072f49;
}

main select {
  outline: none;
  border: none;
  -webkit-appearance: none;
  appearance: none;
  -ms-appearance: none; /* get rid of default appearance for IE8, 9 and 10*/
}

@media only screen and (max-width: 1000px) {
  .card {
    grid-column-end: span 6;
  }
}

@media only screen and (max-width: 700px) {
  .coin-cards-container {
    grid-gap: 20px;
  }
  .card {
    grid-column-end: span 12;
  }
}

@media only screen and (max-width: 500px) {
  .coin-cards-container {
    grid-template-columns: 10px repeat(6, 1fr) 10px;
    grid-gap: 10px;
  }
  .cards {
    grid-column: 2 / span 6;
    grid-template-columns: repeat(6, 1fr);
    grid-gap: 20px;
  }
  .card {
    grid-column-end: span 6;
  }
}

.nav-item {
  color: rgba(238, 238, 238, 0.5);
  margin-right: 16px;
  cursor: pointer;
  transition: 0.1s ease;
}

.nav-item:hover {
  color: rgba(238, 238, 238, 1);
  margin-right: 16px;
  transition: 0.2s ease;
}

.coin-details-container {
  background-color: #041a29;
  border-radius: 12px;
  padding: 1.5rem 3rem;
  margin: 50px auto;
  max-width: 1200px;
}

.coin-details-price-container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  align-content: center;
}

.coin-details-name-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-content: center;
}

.coin-details-name-container img {
  width: 64px;
  height: 64px;
}

.coin-details-name-container h2 {
  margin-bottom: 0;
  margin-left: 10px;
}

.coin-details-statistics-container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
}

.info-container {
  display: flex;
  justify-content: center;
  background-color: #062a42;
  align-items: center;
  padding: 8px;
  border-radius: 5px;
  margin-right: 8px;
  margin-bottom: 8px;
}

.info-container-card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: #052235;
  align-items: center;
  padding: 4px;
  border-radius: 5px;
  margin: 0 10px 10px 10px;
}

.info-container p,
.info-container-card p {
  margin: 0 5px;
  padding: 0;
}

.coin-details-name-container p {
  background-color: #062a42;
  width: 100px;
  text-align: center;
  border-radius: 5px;
  margin: 0 10px;
}

.coin-details-price-container h5 {
  text-align: center;
  border-radius: 5px;
  margin: 0 10px;
}

@media only screen and (max-width: 500px) {
  .coin-details-container {
    background-color: #041a29;
    border-radius: 5px;
    padding: 1em 1rem;
    margin: 50px auto;
    max-width: 95%;
  }
  .info-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: 14px;
    background-color: #062a42;
    align-items: center;
    padding: 2px;
    border-radius: 5px;
    margin-right: 4px;
    margin-bottom: 4px;
  }
}

.news-section-container {
  background-color: #072f49;
  margin-top: 50px;
  padding: 40px 20px 20px 20px;
  max-width: 100%;
}

.news-section-container h2 {
  margin-bottom: 16px;
  margin-left: 16px;
}

.news-headline {
  min-height: 85px;
}

.news-author {
  color: #537895;
}

.news {
  display: flex;
  margin: 1rem;
}

.news-card {
  width: 22%;
  margin: 1rem;
  cursor: default;
}

.news-card img {
  width: 300px;
  height: 200px;
  margin-bottom: 1rem;
  border: 1px solid black;
}

.news-container {
  display: flex;
  justify-content: space-around;
  margin: 1rem;
}

.news-img {
  border: 1px solid black;
  width: 384px;
  height: 256px;
  margin-bottom: 1rem;
}

.news-desc-container {
  display: flex;
  flex-direction: column;
}

@media only screen and (max-width: 500px) {
  .news-card {
    width: 100%;
    margin: 1rem;
  }
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
p {
  color: #e2e2e2;
}

html {
  scroll-behavior: smooth;
}

.overflow {
  width: 1200px;
  height: 70vh;
  overflow: scroll;
  overflow-x: hidden;
  margin-bottom: 100px;
}

::-webkit-scrollbar {
  width: 8px;
  height: 4px;
}
::-webkit-scrollbar-thumb {
  background: #2f84ad;
  border-radius: 0px;
}
::-webkit-scrollbar-thumb:hover {
  background: #2f84ad;
}
::-webkit-scrollbar-track {
  background: #041a29;
  border-radius: 0px;
  box-shadow: inset 0px -3.1px 0px 0px #07263c;
}

