.coin-text {
  margin-bottom: 32px;
  text-align: center;
}

.coin-input {
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border-radius: 4px;
  border: none;
  background-color: #537895;
  background-image: linear-gradient(315deg, #537895 0%, #09203f 74%);
  text-align: left;
  outline: none;
  color: #e2e2e2;
}

.coin-input::-webkit-input-placeholder {
  color: #e2e2e2;
}

.coin-input:-ms-input-placeholder {
  color: #e2e2e2;
}

.coin-input::placeholder {
  color: #e2e2e2;
}

footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  background-color: #041a29;
  width: 100%;
  height: 100px;
}

a:link {
  color: #eee;
  text-decoration: none;
}

